.header {
  position: absolute;
  padding: 10vh 0;
  margin-top: -8vh;
  //height: 10vh;
  width: 100%;
  background-image: linear-gradient(#3463ff, #9db7ff);
  transform: skew(0deg, -2deg);

  .nav {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    align-items: center;
    position: absolute;
    margin-top: -1vh;
    padding: 0.2em;
    width: 100%;
    height: 10vh;
    border: 1px dashed #3c3c3c;

    &__logo {
      display: inline-block;
      font-size: 0;
      img {
        margin-left: 1vw;
        z-index: 999;
        height: 118px;
        width: 150px;
      }
    }

    &__title {
      font-size: 2.5rem;
      width: 25vw;
      margin-left: 1vw;
    }

    &__menu li {
      background-color: #dddddd;
      text-align: center;
      color: #000;
      font-size: 1.2rem;
      display: inline-block;
      padding: 1em;
      margin: 0 0.2em;
      a {
        text-decoration: none;
        color: #000;
        outline: none;
      }
    }

  }
}


